.card {
  background: #fff;
  border-radius: 2px;
  height: 125px;
  margin: 1rem;
  position: relative;
  width: 125px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  background:#6CADDC;
}
.card > .img-container > img {
  width: 100%
}

.wrapper {
  padding-top: 50px;
  background: #ECEFF1;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  padding: 100px;
  padding-bottom: 0;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-content: flex-start;
          align-content: flex-start;
  overflow: auto;
  background-image: url(/Clicky-Game/static/media/house-crests.e804f652.jpg);

}

.title {
  width: 100%;
  text-align: center;
  font-size: 3rem;
  color:rgb(214, 226, 235);
  margin-bottom: 10px;
}

.subtitle {
  width: 100%;
  text-align: center;
  font-size: 2rem;
  color: rgb(214, 226, 235);
  margin-bottom: 10px;
}

.title_area {
  width: 100%;
  text-align: center;
  font-size: 3rem;
  color: rgb(214, 226, 235);
  margin-bottom: 10px;
  background: rgba(128, 0, 0, 0.644);
  border-radius: 5px;
}

nav.navbar.navbar-default {
  margin-bottom: 0;
  width: 100%;
}
.sticky {
  position: fixed;
  top: 0;
  width: 100%;
  height: 80px;
}
.navbar{
  background-image: url(/Clicky-Game/static/media/35dbc35ea7d87a41a3a49e09f74670d9.616a8a70.jpg);

}
.nav-item{
width: 485px;
float: left;
font: bolder;
color: rgba(80, 11, 11, 0.836);
background: rgba(149, 149, 223, 0.719);
margin: 5px;
padding-top: 5px;
border-radius: 10px;

}
a{
  color: rgba(80, 11, 11, 0.836);

}
* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

